











































import { computed, defineComponent } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { Profile } from '@/app/shared/client/type/profile';

function useOpenStoreControlPanelHandler(
  url: string,
): () => void {
  function openStoreControlPanel(): void {
    window.open(url, '_blank');
  }
  return openStoreControlPanel;
}

const ControlPanelLinkButton = defineComponent({
  components: {
    ExternalIcon: Icon.ExternalIcon,
  },
  props: {
    label: {
      type: String,
      default: undefined,
    },
    descriptionLabel: {
      required: true,
      type: String,
    },
    actionLabel: {
      required: true,
      type: String,
    },
    url: {
      type: String,
      default: '',
    },
    horizontalRuleBefore: {
      required: false,
      type: Boolean,
      default: false,
    },
    adviceLabel: {
      required: false,
      type: String,
      default: undefined,
    },
  },
  setup(props, { root }) {
    const i18n = useI18N(root.$store);
    const storeClosed = computed(() => {
      const profile = root.$store.state.profile as Profile;
      return profile.storeClosed;
    });

    const cpUrl = `${root.$store.state.context.cpUrl}${props.url}`;
    const openStoreControlPanel = useOpenStoreControlPanelHandler(cpUrl);

    return {
      i18n,
      storeClosed,
      openStoreControlPanel,
    };
  },
});

export default ControlPanelLinkButton;
